@import url("https://fonts.googleapis.com/css?family=Open+Sans:400,600,700|Poppins:400,600,700");
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

.showcase {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background: #1e1e1e;
}

.showcase-panel {
  width: 80%;
  border-radius: 10px;
  height: 600px;
  overflow: hidden;
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
}

body {
  color: #fff;
  font-size: 16px;
  line-height: 1.6;
  font-family: 'Poppins', sans-serif;
}

a {
  text-decoration: none;
  color: #111111;
  transition: all 200ms ease-in-out;
}

a:hover {
  color: #FF851B;
}

.full-height {
  height: 100%;
}

.flex {
  display: flex;
}

.flex-center {
  justify-content: center;
  align-items: center;
}

.flex-space--between {
  justify-content: space-between;
}

.flex-space--around {
  justify-content: space-around;
}

.flex-align--center {
  align-items: center;
}

.flex-align--end {
  align-items: flex-end;
}

.flex-direction--column {
  flex-direction: column;
}

.flex-end {
  justify-content: flex-end;
}

.element {
  background: #111111;
  height: 100%;
}

.header {
  padding: 15px 30px;
  font-family: 'Poppins', 'Open Sans', sans-serif;
  font-weight: 700;
}

.header-logo {
  letter-spacing: 1px;
  font-weight: 600;
  color: #fff;
}

.header-menu {
  display: flex;
  align-items: center;
  cursor: pointer;
}

.header-menu--name {
  font-size: 13px;
}

.header-menu--burger {
  margin-left: 10px;
}

.header-menu--burger__line {
  width: 20px;
  height: 2px;
  margin-bottom: 4px;
  background: #fff;
}

.header-menu--burger__line:last-child {
  margin-bottom: 0;
}

.content {
  padding: 0 30px;
  padding: 20px 45% 0 15%;
  position: relative;
}

.content-number {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border: 2px solid #fff;
  text-align: center;
  line-height: 36px;
  font-family: 'Poppins', 'Open Sans', sans-serif;
  font-weight: 700;
  margin-left: -20px;
  font-weight: 600;
}

.content-title {
  font-size: 70px;
  line-height: 1;
  letter-spacing: 3px;
  font-family: 'Poppins', 'Open Sans', sans-serif;
  font-weight: 700;
  margin-bottom: 15px;
}

.content-link {
  color: #fff;
  font-weight: 700;
  letter-spacing: 1px;
  position: absolute;
  left: -30px;
  top: calc(100% - 40px);
  transform: rotate(-90deg) translateX(50%);
}

.content-nav {
  position: absolute;
  right: 30px;
  top: calc(50% + 20px);
  transform: translateY(-50%);
}

.content-nav--item {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  margin-bottom: 30px;
}

.content-nav--item:last-child {
  margin-bottom: 0;
}

.content-nav--item:after {
  content: '';
  width: 20px;
  margin-left: 15px;
  height: 20px;
  background: #fff;
  display: inline-block;
}

.footer {
  padding-left: 30px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.footer-copy {
  position: relative;
}

.footer-copy:before {
  content: '';
  width: 25px;
  height: 2px;
  background: #fff;
  display: block;
  margin-bottom: 15px;
}

.footer-copy p {
  font-size: 13px;
}

.footer-copy p:first-child {
  font-weight: 600;
}

.footer-copy p:last-child {
  margin-bottom: 0;
}

.footer-panel {
  box-shadow: 0 10px 28px rgba(0, 0, 0, 0.3), 0 5px 10px rgba(0, 0, 0, 0.4);
  display: flex;
}

.footer-panel--box {
  width: 200px;
  height: 150px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-left: 50px;
  font-size: 14px;
}

.footer-panel--box:last-child {
  padding-left: 0;
  width: auto;
  padding-right: 30px;
}

.footer-panel--title {
  font-weight: 700;
}

.footer-panel--thumb {
  background: #fff;
  height: 40px;
  width: 40px;
}
